import {React,Component} from "react";
import "./Stats.css";
import Values from "./Values.js";
import Media from "react-media";

class Stats extends Component{

	



render(){
	return(
		<div>
			<div id="navBar">
			<span> ADSOUL</span>
			</div>

			<Media queries={{ small: "(max-width: 599px)" }}>
          {matches =>
            matches.small ? (
              <div id="appBarSmall">
				<span id="arrow1" onClick={() =>this.props.onChangeStatus("home",0,"")}> âž” </span>
				<img  alt ="box" src={this.props.setURL}width="40" height= "auto"/>
				<h4>{this.props.setName}</h4>
			</div>
            ) : (
              <div id="appBar">
				<span id="arrow1" onClick={() =>this.props.onChangeStatus("home",0,"")}> âž” </span>
				<img  alt ="box" src={this.props.setURL}width="50" height= "auto"/>
				<h3>{this.props.setName}</h3>
			</div>
            )
          }
        </Media>
			

			<Media queries={{ small: "(max-width: 599px)" }}>
          {matches =>
            matches.small ? (
              <div id="statsBarSmall">
				<span> Date </span>
				<span> Revenue </span>
				<span> Ad Requests </span>
				<span> Ad Responses </span>
				<span> Impressions </span>
				<span> Clicks </span>
				<span> Render Rate </span>
			</div>
            ) : (
              <div id="statsBar">
				<span> Date </span>
				<span> Revenue </span>
				<span> Ad Requests </span>
				<span> Ad Responses </span>
				<span> Impressions </span>
				<span> Clicks </span>
				<span> Render Rate </span>
			</div>
            )
          }
        </Media>
			

			<Values  newIndex = {this.props.setIndex}/>


		</div>
		)
	
}



}



export default Stats;